import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const Psu = ({ forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [psuPrice, setPsuPrice] = useState(0);
  const [name, setName] = useState("");

  useEffect(() => {
    let psu = { name: name, price: psuPrice };
    if (name === "") {
      sessionStorage.removeItem("psu");
    } else {
      sessionStorage.setItem("psu", JSON.stringify(psu));
    }
    forceUpdate();
  }, [psuPrice, name]);

  useEffect(() => {
    const getDatas = async () => {
      const arr = [];
      const url = process.env.REACT_APP_API_URL;
      // const url = "https://rakitpcapi.up.railway.app/api/v1";
      await axios.get(`${url}/psu`).then((res) => {
        let result = res.data;
        result.map((data) => {
          return arr.push({
            value: data.name,
            label: `${data.brand} ${data.watt} ${data.sertifikasi}`,
            url: data.url,
            watt: data.watt,
            sertifikasi: data.sertifikasi,
            price: data.price,
          });
        });
      });
      setOptions(arr);
    };
    getDatas();
  }, []);

  return (
    <>
      <div className="title fw-bold mb-3">Pilih Power Supply</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih RAM"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(psu) => (
              <div className="country-option d-flex ps-1">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={psu.label}
                    height={"60px"}
                    src={psu.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {psu.label} {psu.watt} {psu.sertifikasi} <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(psu.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setPsuPrice(e.price);
                setName(e.label);
              } else {
                setName("");
                setPsuPrice(0);
              }
            }}
          />
        </Container>
      </Form>
      <Container>
        <div className="pt-2 text-bold" id="psuPrice">
          Price : Rp. {numberWithCommas(psuPrice)}
        </div>
      </Container>
    </>
  );
};

export default Psu;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
