import React from "react";
import { Container } from "react-bootstrap";
import img from "../assets/sementara/652324549_5.png";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";

function ListProduct() {
  const kodeProduk = 0;

  return (
    <div className="list-card">
      <div className="cards">
        <img src={img} alt="image" className="img-card" />
        <div className="card-text m-2">
          <h6 className=" fw-bold">Paket Rakitan PC </h6>
          <p className=" text-blue text-sm">Core i7-10700</p>
          <span className=" text-secondary text-sm text-decoration-line-through">
            Rp.10.150.000
          </span>{" "}
          <span className=" p-1 text-white rounded-2 text-center fw-bold text-sm bg-danger ">
            Rp.9.850.000
          </span>{" "}
          <br />
          <a
            href={`rekomendasi/${kodeProduk}`}
            className="btn btn-primary btn-sm text-capitalize m-0 px-2 py-1 mt-2 fw-bold"
          >
            View ...
          </a>
        </div>
      </div>
    </div>
  );
}

const CardProduct = () => {
  return (
    <Container>
      <Carousel
        additionalTransfrom={0}
        arrows
        autoPlaySpeed={3000}
        centerMode={false}
        className=""
        containerClass="container-with-dots"
        dotListClass=""
        draggable
        focusOnSelect={false}
        infinite
        itemClass=""
        keyBoardControl
        minimumTouchDrag={80}
        pauseOnHover
        renderArrowsWhenDisabled={false}
        renderButtonGroupOutside={false}
        renderDotsOutside={false}
        responsive={{
          desktop: {
            breakpoint: {
              max: 3000,
              min: 1920,
            },
            items: 3,
            partialVisibilityGutter: 40,
          },
          desktop2: {
            breakpoint: {
              max: 1919,
              min: 1360,
            },
            items: 3,
            partialVisibilityGutter: 40,
          },
          mobile: {
            breakpoint: {
              max: 450,
              min: 0,
            },
            items: 2,
            partialVisibilityGutter: 30,
          },
          mobile2: {
            breakpoint: {
              max: 767,
              min: 451,
            },
            items: 1,
            partialVisibilityGutter: 30,
          },
          tablet: {
            breakpoint: {
              max: 1359,
              min: 768,
            },
            items: 2,
            partialVisibilityGutter: 30,
          },
        }}
        rewind={false}
        rewindWithAnimation={false}
        rtl={false}
        shouldResetAutoplay
        showDots={false}
        sliderClass=""
        slidesToSlide={1}
        swipeable
      >
        <ListProduct />
        <ListProduct />
        <ListProduct />
        <ListProduct />
        <ListProduct />
      </Carousel>
    </Container>
  );
};

export default CardProduct;
