import React from "react";
import Note from "../components/Note";
import { useRef, useState, useEffect, useReducer } from "react";
import { Compatible } from "../mainComponent/Compatible";
import { OnThisPage } from "../components/OnThisPage";
import CardProduct from "../mainComponent/CardProduct";
import Optional from "../mainComponent/Optional";
import Tambahan from "../mainComponent/Tambahan";
import { Col, Button } from "react-bootstrap";
import GrandTotal from "../mainComponent/GrandTotal";

const Simulasi = () => {
  const [offset, setOffset] = useState(0);
  const [active, setActive] = useState("");
  const [reducerValue, forceUpdate] = useReducer((x) => x + 1, 0);

  const noteRef = useRef(null);
  const vendorRef = useRef(null);
  const moreRef = useRef(null);
  const accRef = useRef(null);
  const handleClick = (ref) => {
    window.scrollTo({
      top: ref.offsetTop - 70,
      left: 0,
      behavior: "smooth",
    });
  };

  const notePosition = (top, height) => {
    if (offset >= top && offset <= height) {
      setActive("note");
    }
  };
  const utamaPosition = (top, height) => {
    if (offset >= top && offset <= height) {
      setActive("utama");
    }
  };

  useEffect(() => {
    const onScroll = () => setOffset(window.pageYOffset);
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <>
      <div className="pt-5 shadow-blur"></div>
      <div className=" mx-8 pt-5">
        <div className=" px-2 px-md-5">
          <div className="row">
            <div className="col-xl-2">
              <OnThisPage
                active={active}
                handleClick={handleClick}
                vendorRef={vendorRef}
                noteRef={noteRef}
                moreRef={moreRef}
                accRef={accRef}
              />
            </div>
            <div className="col-12 col-xl-10">
              <div className=" body mt-6 py-4">
                <CardProduct />
              </div>
              <div className="body mt-3 p-2 p-md-3 p-lg-5">
                <div ref={noteRef}>
                  <Note notePosition={notePosition} />
                </div>
                <div ref={vendorRef}>
                  <Compatible
                    utamaPosition={utamaPosition}
                    forceUpdate={forceUpdate}
                  />
                </div>
                <div ref={moreRef}>
                  <Optional
                    offset={offset}
                    reducerValue={reducerValue}
                    forceUpdate={forceUpdate}
                  />
                </div>
                <div ref={accRef}>
                  <Tambahan offset={offset} forceUpdate={forceUpdate} />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className=" fixed-bottom px-2 px-md-5">
          <GrandTotal reducerValue={reducerValue} />
        </div>
      </div>
    </>
  );
};

export default Simulasi;

export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
