import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const Cassing = ({ formFactor, forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [cassingPrice, setCassingPrice] = useState(0);
  const [showPrice, setShowPrice] = useState("");
  const [name, setName] = useState("");
  const [status, setStatus] = useState(true);

  useEffect(() => {
    localStorage.setItem("status", status);
  }, [status]);

  useEffect(() => {
    let cassing = { name: name, price: cassingPrice };
    if (name === "") {
      localStorage.removeItem("cassing");
    } else {
      localStorage.setItem("cassing", JSON.stringify(cassing));
    }
    forceUpdate();
  }, [cassingPrice, name]);
  useEffect(() => {
    if (formFactor === "" || formFactor === null) {
    } else {
      const getDatas = async () => {
        const arr = [];
        const url = process.env.REACT_APP_API_URL;
        // const url = "https://rakitpcapi.up.railway.app/api/v1";
        await axios
          .get(`${url}/cassing/formFactor/${formFactor}`)
          .then((res) => {
            let result = res.data;
            result.map((data) => {
              return arr.push({
                value: data.name,
                label: data.name,
                url: data.url,
                vendor: data.vendor,
                price: data.price,
              });
            });
          });
        setOptions(arr);
      };
      getDatas();
    }
  }, [formFactor]);

  return (
    <div className="text-black pt-3">
      <div className="title fw-bold mb-3">Pilih Cassing</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih Cassing"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(cassing) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={cassing.label}
                    height={"60px"}
                    src={cassing.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {cassing.label} {cassing.type} - {cassing.slotType} <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(cassing.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setCassingPrice(e.price);
                setShowPrice("d-block");
                setName(e.label);
                setStatus(false);
              } else {
                setCassingPrice(0);
                setName("");
                setStatus(true);
              }
            }}
          />
        </Container>
      </Form>
      <div className={showPrice}>
        <Container>
          <div className="pt-2 text-bold">
            Price : Rp. {numberWithCommas(cassingPrice)}
          </div>
        </Container>
      </div>
    </div>
  );
};

export default Cassing;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
