import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const Mouse = ({ forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [mousePrice, setMousePrice] = useState(0);
  const [name, setName] = useState("");
  useEffect(() => {
    localStorage.setItem("mouse", mousePrice);
    let mouse = { name: name, price: mousePrice };
    if (name === "") {
      localStorage.removeItem("mouse");
    } else {
      localStorage.setItem("mouse", JSON.stringify(mouse));
    }
    forceUpdate();
  }, [mousePrice, name]);

  useEffect(() => {
    const getDatas = async () => {
      const arr = [];
      const url = process.env.REACT_APP_API_URL;
      // const url = "https://rakitpcapi.up.railway.app/api/v1";
      await axios.get(`${url}/mouse`).then((res) => {
        let result = res.data;
        result.map((data) => {
          return arr.push({
            value: data.name,
            label: data.name + " || " + data.koneksi,
            url: data.url,
            rgb: data.rgb,
            price: data.price,
          });
        });
      });
      setOptions(arr);
    };
    getDatas();
  }, []);

  return (
    <>
      <div className="title fw-bold mb-3">Pilih Mouse</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih Mouse"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(mouse) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={mouse.label}
                    height={"60px"}
                    src={mouse.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {mouse.label}
                  {mouse.rgb === "yes" ? "RGB" : ""}
                  <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(mouse.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setMousePrice(e.price);
                setName(e.label);
              } else {
                setMousePrice(0);
                setName("");
              }
            }}
          />
        </Container>
      </Form>
      <Container>
        <div className="pt-2 text-bold">
          Price : Rp. {numberWithCommas(mousePrice)}
        </div>
      </Container>
    </>
  );
};

export default Mouse;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
