import React from "react";
import { Container, Navbar, Nav } from "react-bootstrap";
import { NavLink } from "react-router-dom";

const Header = () => {
  return (
    <Navbar
      bg="dark"
      variant="dark"
      expand="lg"
      fixed="top"
      className=" shadow"
    >
      <Container>
        <Navbar.Brand href="#home" className=" fs-4 fw-bold py-2">
          Rakit PC Simulasi
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className=" me-auto"></Nav>
          <Nav>
            <NavLink to="/" className=" nav-link ">
              Home
            </NavLink>
            <NavLink to="/simulasi" className=" nav-link ">
              Simulasi
            </NavLink>
            <NavLink to="/rekomendasi" className=" nav-link ">
              Rekomendasi
            </NavLink>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;
