import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const Mousepad = ({ forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [mousepadPrice, setMousepadPrice] = useState(0);
  const [name, setName] = useState("");
  useEffect(() => {
    let mousepad = { name: name, price: mousepadPrice };
    if (name === "") {
      localStorage.removeItem("mousepad");
    } else {
      localStorage.setItem("mousepad", JSON.stringify(mousepad));
    }

    forceUpdate();
  }, [mousepadPrice, name]);

  useEffect(() => {
    const getDatas = async () => {
      const arr = [];
      const url = process.env.REACT_APP_API_URL;
      // const url = "https://rakitpcapi.up.railway.app/api/v1";
      await axios.get(`${url}/mousepad`).then((res) => {
        let result = res.data;
        result.map((data) => {
          return arr.push({
            value: data.name,
            label: data.name,
            url: data.url,
            size: data.size,
            ketebalan: data.ketebalan,
            price: data.price,
          });
        });
      });
      setOptions(arr);
    };
    getDatas();
  }, []);

  return (
    <>
      <div className="title fw-bold mb-3">Pilih Mousepad</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih Mousepad"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(mousepad) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={mousepad.label}
                    height={"60px"}
                    src={mousepad.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {mousepad.value} || {mousepad.label}
                  <span className=" text-capitalize text-sm">
                    {mousepad.size}
                    {mousepad.ketebalan} <br />
                  </span>
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(mousepad.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setMousepadPrice(e.price);
                setName(e.label);
              } else {
                setMousepadPrice(0);
                setName("");
              }
            }}
          />
        </Container>
      </Form>
      <Container>
        <div className="pt-2 text-bold">
          Price : Rp. {numberWithCommas(mousepadPrice)}
        </div>
      </Container>
    </>
  );
};

export default Mousepad;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
