import React from "react";

export const OnThisPage = ({
  active,
  handleClick,
  noteRef,
  vendorRef,
  moreRef,
  accRef,
}) => {
  return (
    <div className="body text-secondary p-3 d-none d-xl-block on-this-page">
      <div className=" fw-bold text-black">On This Page</div>
      <div
        className={
          active === "note" ? "cursor-pointer text-black" : "cursor-pointer"
        }
        onClick={() => {
          handleClick(noteRef.current);
        }}
      >
        Note
      </div>
      <div
        className={
          active === "utama" ? "cursor-pointer text-black" : "cursor-pointer"
        }
        onClick={() => handleClick(vendorRef.current)}
      >
        Komponen Utama
      </div>
      <div
        className={
          active === "optional" ? "cursor-pointer text-black" : "cursor-pointer"
        }
        onClick={() => handleClick(moreRef.current)}
      >
        Komponen Optional
      </div>
      <div
        className={
          active === "tambahan" ? "cursor-pointer text-black" : "cursor-pointer"
        }
        onClick={() => handleClick(accRef.current)}
      >
        Komponen Tambahan
      </div>
    </div>
  );
};
