import React from "react";
import Psu from "../components/Psu";
import VgaCard from "../components/VgaCard";
import { useEffect, useState } from "react";

const Optional = ({ reducerValue, forceUpdate }) => {
  const [status, setStatus] = useState(true);
  useEffect(() => {
    if (localStorage.getItem("status") === null) {
      setStatus(true);
    } else {
      setStatus(localStorage.getItem("status"));
    }
  }, [reducerValue]);

  return (
    <div className="komponen ">
      <div className=" fw-bold fs-4 pt-4 main-komponen">Komponen Optional</div>
      <div className="container text-black">
        <VgaCard forceUpdate={forceUpdate} status={status} />
        <br />
        <Psu forceUpdate={forceUpdate} />
      </div>
    </div>
  );
};

export default Optional;
