import React from "react";
import { Modal, Table } from "react-bootstrap";

const MoboModals = ({ data, handleClose, show }) => {
  const numberWithCommas = (x) => {
    if (x === "" || x === null || x === undefined) {
    } else {
      const hasil = x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      return hasil;
    }
  };
  return (
    <>
      <Modal size="xl" show={show} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>
            SPESIFIKASI MOTHERBOARD " {data.motherboardName} "
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Table striped bordered hover variant="light" className="modalTable">
            <tbody>
              <tr>
                <td className=" text-black">CHIPSET</td>
                <td>{data.motherboardChipset}</td>
              </tr>
              <tr>
                <td className=" text-black">SOCKET</td>
                <td>{data.processorSocket}</td>
              </tr>
              <tr>
                <td className=" text-black">MEMORI</td>
                <td>{data.memory}</td>
              </tr>
              <tr>
                <td className=" text-black">JUMLAH SLOT MEMORI</td>
                <td>{data.memorySlot}</td>
              </tr>
              <tr>
                <td className=" text-black">PORT SATA</td>
                <td>{data.sataPort}</td>
              </tr>
              <tr>
                <td className=" text-black">SLOT M.2</td>
                <td>{data.m2Slot}</td>
              </tr>
              <tr>
                <td className=" text-black overflow-auto">USB PORT</td>
                <td>{data.usbPort}</td>
              </tr>
              <tr>
                <td className=" text-black">DISPLAY</td>
                <td>{data.display}</td>
              </tr>
              <tr>
                <td className=" text-black">FORM FACTOR</td>
                <td>{data.formFactor}</td>
              </tr>
              <tr>
                <td className=" text-black">PORT LAN</td>
                <td>{data.lanPort}</td>
              </tr>
              <tr>
                <td className=" text-black">HARGA</td>
                <td>{`Rp. ${numberWithCommas(data.motherboardPrice)}`}</td>
              </tr>
              <tr></tr>
            </tbody>
          </Table>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default MoboModals;
