import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import { Container, Form } from "react-bootstrap";
import Motherboard from "./Motherboard";
import Select from "react-select";

const Processor = ({ type, forceUpdate }) => {
  const [socket, setSocket] = useState("");
  const [showProcessor, setShowProcessor] = useState("d-none");
  const [options, setOptions] = useState("");
  const [processorPrice, setProcessorPrice] = useState(0);
  const [name, setName] = useState("");
  const [current, setCurrent] = useState("");
  useEffect(() => {
    let processor = { name: name, price: processorPrice };
    if (name === "") {
      localStorage.removeItem("processor");
    } else {
      localStorage.setItem("processor", JSON.stringify(processor));
    }
    forceUpdate();
  }, [processorPrice, name]);

  useEffect(() => {
    if (type === "" || type === null) {
    } else {
      const getDatas = async () => {
        const arr = [];
        const url = process.env.REACT_APP_API_URL;
        // const url = "https://rakitpcapi.up.railway.app/api/v1";
        await axios.get(`${url}/processorList/type/${type}`).then((res) => {
          let result = res.data;
          result.map((data) => {
            return arr.push({
              value: data.processorFullName,
              label: data.processorFullName,
              socket: data.processorSocket,
              price: data.price,
            });
          });
        });
        setOptions(arr);
        setShowProcessor("d-none");
      };
      getDatas();
    }
  }, [type]);

  return (
    <div className="text-black pt-3">
      <div className="title fw-bold mb-3">Pilih Processor {type}</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih Processor"
            isClearable={true}
            isSearchable={true}
            name="processor"
            options={options}
            formatOptionLabel={(processor) => (
              <div className="processor-option ms-2">
                <span className="fw-bold">{processor.label}</span> <br />
                <span className=" text-blue">
                  Rp. {numberWithCommas(processor.price)}
                </span>
              </div>
            )}
            onChange={(e) => {
              if (e !== null) {
                setSocket(e.socket);
                setProcessorPrice(e.price);
                setCurrent(e.label);
                setName(e.label);
              } else {
                setProcessorPrice(0);
                setName("");
              }
              setShowProcessor("d-block");
            }}
          />
        </Container>
      </Form>
      <div className={showProcessor}>
        <Container>
          <div className="pt-2 text-bold">
            Price : Rp. {numberWithCommas(processorPrice)}
          </div>
        </Container>
        <Motherboard
          socket={socket}
          current={current}
          forceUpdate={forceUpdate}
        />
      </div>
    </div>
  );
};

export default Processor;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
