import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";
import Storage from "./Storage";

const Ram = ({ memory, m2, formFactor, forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [showStorage, setShowStorage] = useState("d-none");
  const [ramPrice, setRamPrice] = useState(0);
  const [name, setName] = useState("");
  useEffect(() => {
    let ram = { name: name, price: ramPrice };
    if (name === "") {
      localStorage.removeItem("ram");
    } else {
      localStorage.setItem("ram", JSON.stringify(ram));
    }
    forceUpdate();
  }, [name, ramPrice]);

  useEffect(() => {
    if (memory === "" || memory === null) {
    } else {
      const getDatas = async () => {
        const arr = [];
        const url = process.env.REACT_APP_API_URL;
        // const url = "https://rakitpcapi.up.railway.app/api/v1";
        await axios.get(`${url}/memory/type/${memory}`).then((res) => {
          let result = res.data;
          result.map((data) => {
            return arr.push({
              value: data.name + data.size,
              label: data.name,
              url: data.url,
              size: data.size,
              type: data.type,
              price: data.price,
            });
          });
        });
        setOptions(arr);
        setShowStorage("d-none");
      };
      getDatas();
    }
  }, [memory]);

  return (
    <div className="text-black pt-3">
      <div className="title fw-bold mb-3">Pilih RAM "{memory}"</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih RAM"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(ram) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={ram.label}
                    height={"60px"}
                    src={ram.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {ram.label} {ram.size} {ram.type} <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(ram.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setShowStorage("d-block");
                setRamPrice(e.price);
                setName(e.value);
              } else {
                setRamPrice(0);
                setName("");
              }
            }}
          />
        </Container>
      </Form>
      <Container>
        <div className="pt-2 text-bold">
          Price : Rp. {numberWithCommas(ramPrice)}
        </div>
      </Container>
      <div className={showStorage}>
        <Storage m2={m2} formFactor={formFactor} forceUpdate={forceUpdate} />
      </div>
    </div>
  );
};

export default Ram;

export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
