import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const Keyboard = ({ forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [keyboardPrice, setKeyboardPrice] = useState(0);
  const [name, setName] = useState("");
  useEffect(() => {
    let keyboard = { name: name, price: keyboardPrice };
    if (name === "") {
      localStorage.removeItem("keyboard");
    } else {
      localStorage.setItem("keyboard", JSON.stringify(keyboard));
    }
    forceUpdate();
  }, [keyboardPrice, name]);

  useEffect(() => {
    const getDatas = async () => {
      const arr = [];
      const url = process.env.REACT_APP_API_URL;
      // const url = "https://rakitpcapi.up.railway.app/api/v1";
      await axios.get(`${url}/keyboard`).then((res) => {
        let result = res.data;
        result.map((data) => {
          return arr.push({
            value: data.name,
            label: data.name,
            url: data.url,
            type: data.type,
            koneksi: data.koneksi,
            size: data.size,
            switchType: data.switchType,
            rgb: data.rgb,
            price: data.price,
          });
        });
      });
      setOptions(arr);
    };
    getDatas();
  }, []);

  return (
    <>
      <div className="title fw-bold mb-3">Pilih Keyboard</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih Keyboard"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(keyboard) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={keyboard.label}
                    height={"60px"}
                    src={keyboard.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {keyboard.value} <br />
                  <span className=" text-sm fst-italic text-lowercase">
                    {keyboard.koneksi} {keyboard.type} {keyboard.size}{" "}
                  </span>
                  <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(keyboard.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setKeyboardPrice(e.price);
                setName(e.label);
              } else {
                setName("");
                setKeyboardPrice(0);
              }
            }}
          />
        </Container>
      </Form>
      <Container>
        <div className="pt-2 text-bold">
          Price : Rp. {numberWithCommas(keyboardPrice)}
        </div>
      </Container>
    </>
  );
};

export default Keyboard;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
