import React from "react";
import { useParams } from "react-router-dom";

const Produk = () => {
  const { kodeProduk } = useParams();
  return (
    <>
      <div className=" pt-4 shadow-blur"></div>
      <div className=" pt-4 shadow-blur"></div>
      <div className=" pt-4 shadow-blur"></div>
      <div className=" d-flex justify-content-center align-content-center text-black">
        <h1>Kode Produk : {kodeProduk}</h1>
      </div>
    </>
  );
};

export default Produk;
