import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const GamingDesk = ({ forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [gamingDeskPrice, setGamingDeskPrice] = useState(0);
  const [name, setName] = useState("");
  useEffect(() => {
    let gamingDesk = { name: name, price: gamingDeskPrice };
    if (name === "") {
      localStorage.removeItem("gamingDesk");
    } else {
      localStorage.setItem("gamingDesk", JSON.stringify(gamingDesk));
    }
    forceUpdate();
  }, [gamingDeskPrice, name]);

  useEffect(() => {
    const getDatas = async () => {
      const arr = [];
      const url = process.env.REACT_APP_API_URL;
      // const url = "https://rakitpcapi.up.railway.app/api/v1";
      await axios.get(`${url}/gamingDesk`).then((res) => {
        let result = res.data;
        result.map((data) => {
          return arr.push({
            value: data.name,
            label: data.name,
            url: data.url,
            size: data.size,
            price: data.price,
          });
        });
      });
      setOptions(arr);
    };
    getDatas();
  }, []);

  return (
    <>
      <div className="title fw-bold mb-3">Pilih Gaming Desk</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih GamingDesk"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(gamingDesk) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={gamingDesk.label}
                    height={"60px"}
                    src={gamingDesk.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {gamingDesk.value} || {gamingDesk.label} {gamingDesk.size}
                  <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(gamingDesk.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setGamingDeskPrice(e.price);
                setName(e.label);
              } else {
                setGamingDeskPrice(0);
                setName("");
              }
            }}
          />
        </Container>
      </Form>
      <Container>
        <div className="pt-2 text-bold">
          Price : Rp. {numberWithCommas(gamingDeskPrice)}
        </div>
      </Container>
    </>
  );
};

export default GamingDesk;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
