import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const VgaCard = ({ forceUpdate, status }) => {
  const [options, setOptions] = useState("");
  const [vgaPrice, setVgaPrice] = useState(0);
  const [name, setName] = useState("");
  const [reqPsu, setReqPsu] = useState(0);

  useEffect(() => {
    const getDatas = async () => {
      const arr = [];
      const url = process.env.REACT_APP_API_URL;
      // const url = "https://rakitpcapi.up.railway.app/api/v1";
      await axios.get(`${url}/vga`).then((res) => {
        let result = res.data;
        result.map((data) => {
          return arr.push({
            value: data.name,
            label: `${data.name} ${data.memorySize} ${data.memoryType}`,
            url: data.url,
            size: data.memorySize,
            type: data.memoryType,
            reqPsu: data.reqPsu,
            price: data.price,
          });
        });
      });
      setOptions(arr);
    };
    getDatas();
  }, []);

  useEffect(() => {
    let vga = { name: name, price: vgaPrice };
    if (name === "") {
      localStorage.removeItem("vga");
    } else {
      localStorage.setItem("vga", JSON.stringify(vga));
    }
    forceUpdate();
  }, [vgaPrice, name]);

  const handleChange = (e) => {
    if (e !== null) {
      setName(e.label);
      setVgaPrice(e.price);
      setReqPsu(e.reqPsu);
    } else {
      setName("");
      setVgaPrice(0);
      setReqPsu(0);
    }
  };

  return (
    <>
      <div className="title fw-bold mb-3 pt-3">Pilih VGA Card</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih RAM"
            isClearable
            isSearchable={true}
            options={options}
            formatOptionLabel={(vga) => (
              <div className="country-option d-flex ms-2">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={vga.label}
                    height={"60px"}
                    src={vga.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {vga.label} <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(vga.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => handleChange(e)}
          />
        </Container>
      </Form>
      <Container>
        <div className="pt-2 text-bold">
          <div className=" fw-bold text-success">
            Request minimal PSU : {reqPsu} <br />
          </div>
          Price : Rp. {numberWithCommas(vgaPrice)}
        </div>
      </Container>
    </>
  );
};

export default VgaCard;

export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
