import React from "react";
import GamingChair from "../components/GamingChair";
import GamingDesk from "../components/GamingDesk";
import Headset from "../components/Headset";
import Keyboard from "../components/Keyboard";
import Monitor from "../components/Monitor";
import Mouse from "../components/Mouse";
import Mousepad from "../components/Mousepad";
import Speaker from "../components/Speaker";

const Tambahan = ({ forceUpdate }) => {
  return (
    <div className="komponen ">
      <div className=" fw-bold fs-4 pt-4 main-komponen">Komponen Tambahan</div>
      <div className="container text-black">
        <Monitor forceUpdate={forceUpdate} /> <br />
        <Keyboard forceUpdate={forceUpdate} /> <br />
        <Mouse forceUpdate={forceUpdate} /> <br />
        <Headset forceUpdate={forceUpdate} /> <br />
        <Speaker forceUpdate={forceUpdate} /> <br />
        <GamingChair forceUpdate={forceUpdate} /> <br />
        <GamingDesk forceUpdate={forceUpdate} /> <br />
        <Mousepad forceUpdate={forceUpdate} />
      </div>
    </div>
  );
};

export default Tambahan;
