import React, { useState } from "react";
import { Row, Col, Card, Form } from "react-bootstrap";
import intel from "../assets/images/vendor/intel.png";
import amd from "../assets/images/vendor/amd.png";
import Seri from "./Seri";

const Vendor = ({ forceUpdate }) => {
  const [bgIntel, setBgIntel] = useState("");
  const [bgAmd, setBgAmd] = useState("");
  const [show, setShow] = useState("d-none");
  const [vendor, setVendor] = useState("");
  const [current, setCurrent] = useState("");

  const choseIntel = (e) => {
    e.preventDefault();
    if (bgIntel === "") {
      setBgIntel("circle-on");
      setShow("d-block");
      setVendor("Intel");
      setBgAmd("");
      setCurrent("intel");
    } else {
      setBgIntel("");
      setShow("d-none");
    }
  };
  const choseAmd = (e) => {
    e.preventDefault();
    if (bgAmd === "") {
      setBgAmd("circle-on");
      setShow("d-block");
      setVendor("AMD");
      setBgIntel("");
      setCurrent("amd");
    } else {
      setBgAmd("");
      setShow("d-none");
    }
  };
  return (
    <div className="text-black ">
      <div className="title fw-bold mb-3">Pilih Brand Processor</div>
      <Form id="brand">
        <Row className=" justify-content-center">
          <Col xs="5" lg="2" md="3" sm="4">
            <Card onClick={choseIntel}>
              <div className=" d-flex justify-content-end">
                <div className={`circle ${bgIntel}`}></div>
              </div>
              <Card.Img className="card-height" variant="top" src={intel} />
            </Card>
          </Col>
          <Col lg="2" md="3" sm="4" xs="5">
            <Card onClick={choseAmd}>
              <div className=" d-flex justify-content-end">
                <div className={`circle ${bgAmd}`}></div>
              </div>
              <Card.Img className="card-width" variant="top" src={amd} />
            </Card>
          </Col>
        </Row>
      </Form>
      <div className={show}>
        <Seri vendor={vendor} current={current} forceUpdate={forceUpdate} />
      </div>
    </div>
  );
};

export default Vendor;
