import React from "react";
import { Form, Row, Col, Card } from "react-bootstrap";
import { useState, useEffect } from "react";
import axios from "axios";
import Processor from "./Processor";

const Seri = ({ vendor, current, forceUpdate }) => {
  const [type, setType] = useState("");
  const [show, setShow] = useState("d-none");
  const [data, setData] = useState([]);
  const [currentId, setCurrentId] = useState("");

  useEffect(() => {
    const getDatas = async () => {
      setCurrentId(current);
      const url = process.env.REACT_APP_API_URL;
      // const url = "https://rakitpcapi.up.railway.app/api/v1";
      const response = await axios.get(`${url}/processor/vendor/${vendor}`);
      setData(response.data);
      setShow("d-none");
    };
    getDatas();
  }, [vendor]);

  const handleClick = (e) => {
    e.preventDefault();
    setShow("d-block");
    let chose = e.target.id;
    if (chose === null || chose === "") {
      chose = e.target.alt;
    }
    setType(chose);
  };
  var mainElement = document.querySelectorAll(".seri .card");
  if (currentId === "amd" || currentId === "intel") {
    mainElement.forEach((element) => {
      element.classList.add("border-0");
    });
  }

  function cardBorder(type) {
    var element = document.getElementById(`processor${type}`);
    var currentElement = document.getElementById(`processor${currentId}`);
    setCurrentId(type);
    element.classList.remove("border-0");
    if (currentElement !== null) {
      currentElement.classList.add("border-0");
    }
    if (currentId === type) {
      element.classList.remove("border-0");
    }
  }
  return (
    <div className="text-black pt-3">
      <div className="title fw-bold mb-3">Pilih Seri Processor</div>
      <Form>
        <div className="seri">
          <Row className=" justify-content-md-center">
            {data.map((datas, index) => (
              <Col xl="2" lg="3" md="4" sm="4" xs="4" key={index}>
                <Card
                  className="card-seri mb-4 mb-xl-0 border-0"
                  onClick={(e) => {
                    handleClick(e);
                    cardBorder(datas.type);
                  }}
                  id={`processor${datas.type}`}
                >
                  <Card.Img variant="none" src={datas.url} alt={datas.type} />
                  <Card.Text id={datas.type}>{datas.name}</Card.Text>
                </Card>
              </Col>
            ))}
          </Row>
        </div>
      </Form>
      {show === "d-block" ? (
        <Processor type={type} forceUpdate={forceUpdate} />
      ) : (
        ""
      )}
    </div>
  );
};

export default Seri;
