import React from "react";
import { Button } from "react-bootstrap";
import img from "../assets/images/HERO-IMG.png";
import { Container } from "react-bootstrap";
import { NavLink } from "react-router-dom";

const Hero = () => {
  return (
    <div className="hero">
      <div className="hero-bg d-flex align-items-center ">
        <Container className=" d-flex align-items-center">
          <div className="left">
            <div className=" fs-2 fw-bold text-white text-shadow pb-1">
              Temukan Perangkat <br />
              <span className=" text-warning">DESKTOP</span> Kebutuhan Anda !
            </div>
            <div className="fs-5 text-white fw-normal text-shadow pb-4">
              Custom berbagai komponen dan cek estimasi biaya yang dibutuhkan
              untuk PC sesuai keinginan anda
            </div>
            <Button className="btn-danger rounded-5">
              <NavLink
                to="/simulasi"
                className=" text-white text-decoration-none"
              >
                Custom Sekarang !
              </NavLink>
            </Button>
          </div>
          <div className="right">
            <img src={img} alt="" />
          </div>
        </Container>
      </div>
    </div>
  );
};

export default Hero;
