import React from "react";
import { useState, useEffect } from "react";
import { Button, Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";
import Cassing from "./Cassing";

const StorageOptions = ({
  options,
  setShowCassing,
  setPrice,
  setName,
  setCount,
  count,
  showButton,
  resetItem2,
}) => {
  return (
    <Form>
      <Container>
        <div className="row">
          <Select
            className="basic-single col-11"
            classNamePrefix="select"
            placeholder="Pilih Storage"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(storage) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={storage.label}
                    height={"60px"}
                    src={storage.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {storage.label} {storage.size} {storage.type} -{" "}
                  {storage.slotType} <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(storage.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setShowCassing("d-block");
                const price = Number(e.price);
                setPrice(price);
                setName(e.value);
              } else {
                setPrice(0);
                setName("");
              }
            }}
          />
          {showButton === true ? (
            count === false ? (
              <Button
                className="col-1"
                variant="success"
                onClick={() => {
                  setCount(true);
                }}
              >
                +
              </Button>
            ) : (
              <Button
                className="col-1"
                variant="secondary"
                onClick={() => {
                  setCount(false);
                  resetItem2();
                }}
              >
                -
              </Button>
            )
          ) : (
            ""
          )}
        </div>
      </Container>
    </Form>
  );
};

const Storage = ({ m2, formFactor, forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [showCassing, setShowCassing] = useState("d-none");
  const [storagePrice, setStoragePrice] = useState(0);
  const [name, setName] = useState("");
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [price, setPrice] = useState(0);
  const [price2, setPrice2] = useState(0);
  const [count, setCount] = useState(false);

  useEffect(() => {
    setStoragePrice(price + price2);
    if (name2 === null || name2 === "") {
      setName(name1);
    } else {
      setName(name1 + " + " + name2);
    }
  }, [price, price2, name1, name2]);
  useEffect(() => {
    let storage = { name: name, price: storagePrice };
    if (name === "") {
      localStorage.removeItem("storage");
    } else {
      localStorage.setItem("storage", JSON.stringify(storage));
    }
    forceUpdate();
  }, [storagePrice, name]);

  useEffect(() => {
    if (m2 !== "-") {
      const getDatas = async () => {
        const arr = [];
        // const url = process.env.REACT_APP_API_URL;
        await axios.get(`http://localhost:5000/api/v1/storage`).then((res) => {
          let result = res.data;
          result.map((data) => {
            return arr.push({
              value: data.name + " " + data.size + " " + data.slotType,
              label: data.name,
              url: data.url,
              size: data.size,
              type: data.type,
              slotType: data.slotType,
              price: data.price,
            });
          });
        });
        setOptions(arr);
        setShowCassing("d-none");
      };
      getDatas();
    } else {
      const getDatas = async () => {
        const arr = [];
        const url = process.env.REACT_APP_API_URL;
        // const url = "https://rakitpcapi.up.railway.app/api/v1";
        await axios.get(`${url}/storage/slotType/sata`).then((res) => {
          let result = res.data;
          result.map((data) => {
            return arr.push({
              value: data.name + " " + data.size + " " + data.slotType,
              label: data.name,
              url: data.url,
              size: data.size,
              type: data.type,
              slotType: data.slotType,
              price: data.price,
            });
          });
        });
        setOptions(arr);
        setShowCassing("d-none");
      };
      getDatas();
    }
  }, [m2]);

  const resetItem2 = () => {
    setName2("");
    setPrice2(0);
  };

  return (
    <div className="text-black pt-3">
      <div className="title fw-bold mb-3">Pilih Storage</div>
      <StorageOptions
        options={options}
        setShowCassing={setShowCassing}
        setStoragePrice={setStoragePrice}
        setPrice={setPrice}
        setName={setName1}
        setCount={setCount}
        count={count}
        showButton={true}
        resetItem2={resetItem2}
      />
      <br />
      {count === true ? (
        <StorageOptions
          options={options}
          setShowCassing={setShowCassing}
          showButton={false}
          setPrice={setPrice2}
          setName={setName2}
        />
      ) : (
        ""
      )}
      <Container>
        <div className="pt-2 text-bold">
          Price : Rp. {numberWithCommas(storagePrice)}
        </div>
      </Container>
      <div className={showCassing}>
        <Cassing formFactor={formFactor} forceUpdate={forceUpdate} />
      </div>
    </div>
  );
};

export default Storage;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
