import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const Monitor = ({ forceUpdate }) => {
  const [options, setOptions] = useState("");
  const [monitorPrice, setMonitorPrice] = useState(0);
  const [name, setName] = useState("");
  useEffect(() => {
    let monitor = { name: name, price: monitorPrice };
    if (name === "") {
      localStorage.removeItem("monitor");
    } else {
      localStorage.setItem("monitor", JSON.stringify(monitor));
    }
    forceUpdate();
  }, [monitorPrice, name]);

  useEffect(() => {
    const getDatas = async () => {
      const arr = [];
      const url = process.env.REACT_APP_API_URL;
      // const url = "https://rakitpcapi.up.railway.app/api/v1";
      await axios.get(`${url}/monitor`).then((res) => {
        let result = res.data;
        result.map((data) => {
          return arr.push({
            value: data.name,
            label:
              data.name +
              " || " +
              data.brand +
              " " +
              data.panel +
              " " +
              data.resolusi,
            url: data.url,
            price: data.price,
          });
        });
      });
      setOptions(arr);
    };
    getDatas();
  }, []);

  return (
    <>
      <div className="title fw-bold mb-3">Pilih Monitor</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih Monitor"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(monitor) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img
                    width={"60px"}
                    alt={monitor.label}
                    height={"60px"}
                    src={monitor.url}
                  />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {monitor.label}
                  <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(monitor.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              if (e !== null) {
                setMonitorPrice(e.price);
                setName(e.label);
              } else {
                setName("");
                setMonitorPrice(0);
              }
            }}
          />
        </Container>
      </Form>
      <Container>
        <div className="pt-2 text-bold">
          Price : Rp. {numberWithCommas(monitorPrice)}
        </div>
      </Container>
    </>
  );
};

export default Monitor;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
